@import url(../App.css);

/* ---------------------------------------ul------------------------------  */
.list-items {
  gap: 2rem;
  width: 80%;
  display: grid;
  margin: 2rem auto;
  max-height: fit-content;
  grid-auto-rows: max-content;
}
.list-items li {
  border-radius: 4rem;
}
.card-container {
  height: 100%;
  display: flex;
  color: var(--text);
  align-items: stretch;
  border-radius: .5rem;
  flex-direction: column;
  justify-content: space-between;
  background-color: var(--elements);
}
.img-flag {
  border-radius: .5rem .5rem 0 0;
  max-height: 300px;
}
.text-area {
  margin: 2rem;
  display: flex;
  align-items: start;
  flex-direction: column;
  justify-content: center;
  color: var(--text);
}
.text-area h1 {
  font-size: 1.2rem;
}
.text-area p {
  margin: 1rem 0;
  font-weight: 600;
  text-transform: capitalize;
}
.text-area p span {
  font-weight: 300;
  color: var(--input);
}
/* ---------------------------------------------search-------------------------------------- */
.hero-nav {
  gap: 1rem;
  width: 80%;
  display: flex;
  cursor: pointer;
  align-items: start;
  flex-direction: column;
  justify-content: space-between;
}
.search-div {
  gap: 1rem;
  border: 0;
  width: 100%;
  display: flex;
  cursor: pointer;
  border-radius: 5px;
  padding-left: 1rem;
  color: var(--text);
  align-items: center;
  justify-content: start;
  background-color: var(--elements);
}
.input[type=text] {
  border: 0;
  width: 100%;
  padding: 1rem;
  font-size: 1.2rem;
  color: var(--text);
  background-color: var(--elements);
}
.input[type=text]::placeholder {
  color: var(--text);
}
.input[type=text]:focus {
  outline: none;
  border: 3px solid #555;
}
.filter-div {
  padding: 1rem;
  width: fit-content;
  border-radius: 5px;
  background-color: var(--elements);
}
select {
  border: 0;
  cursor: pointer;
  font-size: 1.1rem;
  color: var(--text);
  background-color: var(--elements);
}
select option {
  cursor: pointer;
}
@media (min-width: 728px) {
  .nav-text {
    margin: 0 auto;
    width: 90%;
  }
  .hero-nav {
    width: 90%;
    flex-direction: row;    
  }
  .search-div { width: 50%; }
  .list-items {
    width: 90%;
    grid-template-columns: repeat(2, 1fr);
  }
  .brand { font-size: 1.6rem; }
  #theme-changer { font-size: 1.4rem; }
  .text-area h1 { font-size: 1.6rem; }
  .text-area p { font-size: 1.2rem; }
  .input[type=text] { font-size: 1.4rem; }
  select { font-size: 1.4rem;}
  select option { font-size: 1.2rem;}
}

@media (min-width: 1224px) {
  .brand { font-size: 1.8rem; }
  #theme-changer { font-size: 1.6rem; }
  .text-area h1 { font-size: 1.6rem; }
  .text-area p { font-size: 1.2rem; }
  .input[type=text] { font-size: 1.6rem; }
  select { font-size: 1.6rem;}
  select option { font-size: 1.2rem;}
  .search-div { width: 40%; }
  .list-items {
    grid-template-columns: repeat(4, 1fr);
  }
}