@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&display=swap");
:root {
  --text: hsl(0, 0%, 100%);
  --bg: hsl(207, 26%, 17%);
  --input: hsl(0, 0%, 70%);
  --elements: hsl(209, 23%, 22%);
}
[data-theme="light"] {
  --text: hsl(200, 15%, 8%);
  --bg: hsl(0, 0%, 96%);
  --input: hsl(0, 0%, 30%);
  --elements: hsl(0, 0%, 100%);
}  
* {
  margin: 0;
  padding: 0;
  font: inherit;
  box-sizing: border-box;
  transition: all .3s ease-in-out;
  font-family: "Nunito Sans", sans-serif;
}
img, picture, svg, video {
  display: block;
  max-width: 100%;
}
h1, h2, h3, h4, h5, h6 {
  font-weight: 800;
  text-wrap: balance;
}
ul, li {
  list-style: none;
}
p {
  text-wrap: pretty;
}
body {
  color: var(--text);
  background-color: var(--bg);
}
.d-none { display: none; }
.app-container {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;  
}
.nav {
  width: 100%;
  padding: 0 1rem;
  color: var(--text);
  background-color: var(--elements);
}
.nav-text {
  width: 100%;
  height: 6dvh;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.brand {
  font-weight: 700;
  font-size: 1.2rem;
}
.icon {
  width: 20px;
  height: 20px;
  filter: brightness(100) invert(1);
}
#theme-changer {
  border: 0;
  cursor: pointer;
  font-weight: 600;
  font-size: 1.1rem;
  color: var(--text);
  background-color: var(--elements);
  gap: .8rem;
  display: flex;
  align-items: center;
}
.back-to-top {
  position: fixed;
  border: 0;
  right: 1rem;
  bottom: 1rem;
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  color: white;
  background-color: darkgray;
  cursor: pointer;
}
.back-to-top:hover {
  background-color: dodgerblue;
}
.back-to-top:active {
  background-color: blue;
}
